<template id="roof">
  <div>
    <h1>Risk Grading System</h1>
    <input type="text" v-model="msg">
    <button @click="showChildComponentData">评估</button>
    <div v-show="isshow">
      <ul v-for="item in select" :key='item'><li>{{item}}</li></ul>
    </div>
    <P> 已收录 {{ number }} 家机构 </P>
    <router-link to="/About">About</router-link>
  </div>
</template>

<script type="text/javascript">
import jQuery from 'jquery'
import { getAddr } from '../addresses'

var getname = []
export default {
  data () {
    return {
      isshow: true,
      msg: '',
      films: [],
      select: [],
      number: '19982'
    }
  },
  methods: {
    showChildComponentData: function () {
      this.$store.state.keyword = this.msg
      this.$router.push({path: '/Result'})
    }
  },
  watch: {
    msg: function (val, oldVal) {
      if (val.length === 0) {
        this.isshow = false
      } else {
        var key = this.msg
        jQuery.ajax({
          // type: 'post',
          // async: false,
          // // url: 'servlet/helloWorld',
          // url: 'codepoet.cn:8080/servlet/helloWorld',
          // data: {keyword: key},
          // dataType: 'json',
          // success: function (result) {
          type: 'post',
          async: false,
          url: getAddr('/servlet/helloWorld'),
          data: {keyword: key},
          dataType: 'json',
          success: (result) => {
            console.log(result)
            this.select = result.map(v => v.name)
          },
          error: function() {
            console.log('abasdgfasdf')
          }
        })
        this.isshow = true
      }
    }
  }
}
</script>

<style scoped>
ul{
  border:1px solid red;
} 
li{
  height:40px;
  line-height: 40px;
  border-bottom: 1px solid #ddd;
}
.bindingbtn input{
  border:1px solid #333;
  height:44px;
  line-height:44px;
}
</style>



